import React from "react"

import { constants } from "../../../../constants"
import Layout from "../../../components/layout"
import SEO from "../../../components/seo"
import Folders from "../../../components/folders"
import BackButton from "../../../components/back-button"

const Library = ({ location }) => {
  const title = "Librer√≠as"
  const path = constants.LIBRARY_BASE_PATH

  const folders = [
    {
      name: constants.LIBRARY_CHARLIE.title,
      path: `${path}/${constants.LIBRARY_CHARLIE.folder}`,
    },
    {
      name: constants.LIBRARY_LULE.title,
      path: `${path}/${constants.LIBRARY_LULE.folder}`,
    },
  ]

  return (
    <Layout location={location} title={title}>
      <SEO title={title} />
      <h1>{title}</h1>
      <BackButton location={location} path="/wiki" title="Wiki" />
      <Folders folders={folders} location={location} title={title} />
    </Layout>
  )
}

export default Library
